import { appSetting } from 'src/app/app-setting';
import Swal  from 'sweetalert2';
import { orderQtyModels } from './../../model/orderQtyModels';
import { HttpClient } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { outputQtyModel } from 'src/app/model/outputQtyModel';

@Injectable({
  providedIn: 'root'
})
export class OrderQtyService {
  private url = `${this.appSetting.http}://${this.appSetting.apiPort}/api/orderQtyModels`;
  private httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json'
    })
  };
  constructor(private http: HttpClient,private appSetting:appSetting) {}
  get(): Observable<orderQtyModels[]> {
    return this.http.get<orderQtyModels[]>(this.url);
  }
  getSingle(id: number): Observable<orderQtyModels> {
    const searchUrl = `${this.url}/${id}`;
    return this.http.get<orderQtyModels>(searchUrl);
  }
  post(data: orderQtyModels[]): void {

    let tempList=[2,3];
    let dataList:outputQtyModel[]=[];
    
    if(tempList.includes(this.appSetting.sessionUser.userType)){
      this.url=`${this.appSetting.http}://${this.appSetting.apiPort}/api/outputQtyModels`;
      
      data.forEach(x => {
            let z:outputQtyModel={outputQty_ID: 0,
              outputQty: 0,
              sub_item_id: 0,
              main_item_id: 0,
              output_price: 0,
              outputDate: new Date,
              userID: 0};
            z.outputQty_ID=0;
            z.outputQty=x.outputQty;
            z.sub_item_id=x.sub_item_id;
            z.main_item_id=x.main_item_id;
            z.output_price=x.output_price;
            z.outputDate=new  Date;
            z.userID=this.appSetting.sessionUser.userID;
            dataList.push(z);
      });
      let postData:outputQtyModel[]=dataList;
      this.save(postData,'invoice');
    }
    else{
      let postData:orderQtyModels[]=data;
      this.save(postData,'order');
    }

    
  }

  save(postData:any[],msg){
    Swal.fire({
      title: 'System Message',
      html: `Your ${msg} is porcessing...`,
      confirmButtonText: 'Done',
      onBeforeOpen: () => {
        Swal.showLoading();
      }
    });
    this.http.post(this.url, postData, this.httpOptions).subscribe(
      res => {
      
      },
      err => {
         Swal.hideLoading();
        Swal.fire({
          title: 'System Message',
          html: 'Oops your internet Connection is not stable...',
          confirmButtonText: 'Ok'
        });
      },()=>{
        this.appSetting.localShopcart=[];
        this.appSetting.localViewShopcart=[];
         Swal.hideLoading();
      }
    );this.http.post(this.url, postData, this.httpOptions).subscribe(
      res => {
      
      },
      err => {
         Swal.hideLoading();
        Swal.fire({
          title: 'System Message',
          html: 'Oops your internet Connection is not stable...',
          confirmButtonText: 'Ok'
        });
      },()=>{
        this.appSetting.localShopcart=[];
        this.appSetting.localViewShopcart=[];
         Swal.hideLoading();
      }
    );
  }

  put(data: orderQtyModels): void {
    const searchUrl = `${this.url}/${data.orderQty_ID}`;
    this.http.put(searchUrl, data, this.httpOptions).subscribe(
      res => {
   

      },
      err => {
        console.log(err);
      }
    );
  }

  delete(id: number): void {
    const data = null;
    const xhr = new XMLHttpRequest();
    xhr.withCredentials = false;

    xhr.addEventListener('readystatechange', function() {
      if (this.readyState === this.DONE) {
        console.log(this.responseText);
      }
    });

    xhr.open('DELETE', this.url + '/' + id);

    xhr.send(data);
  }
}
