import { OrderDetialService } from './../Services/orderDetial/order-detial.service';
import { OrderService } from './../Services/order/order.service';
import { UserService } from './../Services/User/user.service';
import { UserOrderTrackingService } from './../Services/userOrderTracking/user-order-tracking.service';
import { locationModel } from './../model/locationModel';
import { LocationSaveService } from './../Services/locationSave/location-save.service';
import { priceModel } from './../model/priceModel';
import { UnitService } from './../Services/unit/unit.service';
import { orderQtyViewModel } from './../model/orderQtyViewModel';
import { orderQtyModels } from './../model/orderQtyModels';
import  Swal  from 'sweetalert2';
import { appSetting } from './../app-setting';
import { Component, SimpleChanges, IterableDiffer, IterableDiffers } from '@angular/core';
import { OrderQtyService } from '../Services/orderQty/order-qty.service';
import { Geolocation } from '@ionic-native/geolocation/ngx';
import { InAppBrowser, InAppBrowserOptions  } from '@ionic-native/in-app-browser/ngx';
@Component({
  selector: 'app-tab2',
  templateUrl: 'tab2.page.html',
  styleUrls: ['tab2.page.scss']
})
export class Tab2Page {
  options : InAppBrowserOptions = {
    location : 'yes',//Or 'no' 
    hidden : 'no', //Or  'yes'
    zoom : 'no',//Android only ,shows browser zoom controls 
    hideurlbar:'yes',//Or 'no'

};
  constructor(public appSetting:appSetting,

    
    private orderService:OrderQtyService,
    private orderTrackingService:UserOrderTrackingService,
    private UnitService:UnitService,
    private locationService:LocationSaveService,
    private UserService:UserService,
    private orderDetialService:OrderDetialService,
    private orderModelService:OrderService,
    private iab:InAppBrowser,
    private geolocation: Geolocation) {       
    this.appSetting.checkOut=0;
  }
  orderTracking=0;
  invoice=0;
  totalQty:number=0;
  totalAmount:number=0;

  toSignUp(){
    this.appSetting.signIn=0;this.appSetting.signUp=1;
  }

  googleMap(){
  let url=``;

  let me:locationInterface={location_ltd:'0',location_lng:'0'};
  let user:locationInterface={location_ltd:'0',location_lng:'0'};
  this.locationService.get(this.appSetting.sessionUserDelivery.userID).subscribe(
    x=>{
      user.location_ltd=x.location_ltd.toString();
      user.location_lng=x.location_lng.toString();
    },
    err=>console.log(err),
    ()=>{
      this.geolocation.getCurrentPosition().then((resp) => {
      
          me.location_ltd=resp.coords.latitude.toString();
          me.location_lng=resp.coords.longitude.toString();
          url=`https://www.google.com/maps/dir/${me.location_ltd},${me.location_lng}/${user.location_ltd},${user.location_lng}/@${user.location_ltd},${user.location_lng}`;
          let target = "_blank";
          const browser = this.iab.create(url,target,this.options);

        
          browser.on('loadstop').subscribe(event => {
             browser.insertCSS({ code: "body{color: red;" });
          });
          
          
        }
  )
    /*this.browserTab.isAvailable()
    .then(isAvailable => {
      if (isAvailable) {
        this.browserTab.openUrl(url);
      } else {
        // open URL with InAppBrowser instead or SafariViewController
      }
    });
  })*/
})
}
  orderListRefresh(){
    Swal.fire({
      title: 'Please wait',
      html: 'System is processing for your request',
      onBeforeOpen: () => {
        Swal.showLoading()}});
    if(this.appSetting.deliveryOrOrder===0){
    this.orderTrackingService.getAll().subscribe(
      x=>this.appSetting.userOrderTrackingList=x,
      err=>console.log(err),()=>{
        Swal.hideLoading();
    })}
    else if(this.appSetting.deliveryOrOrder===1){
      this.orderTrackingService.getDelivery().subscribe(
        x=>this.appSetting.userOrderTrackingList=x,
        err=>console.log(err),()=>{
          Swal.hideLoading();
      })
    }
   }

  ngDoCheck() {
      this.totalCalculator();
  }

  
  orderTrackingDetialPage(id){
    this.invoice=0;
    this.appSetting.userOrderTrackingListTemp=[];
    this.appSetting.userOrderTrackingList.forEach(x=>{
      if(x.orderID===id){      
        this.UserService.getSingle(x.userID).subscribe(x=>this.appSetting.sessionUserDelivery=x,
          err=>console.log(err),
          ()=>{
            this.appSetting.userOrderTrackingListTemp.push(x);
          })
      }
    });
    this.orderTracking=1;
    this.invoice=+id;
   }
   onApprove(){
        this.serviceAR(1);
    }

    onReject(){
        this.serviceAR(2);
    }

   serviceAR(status){
    if(this.appSetting.deliveryOrOrder===0){
      if(status!==0){
      this.orderDetialService.put(this.invoice,status);
      this.orderTracking=0;
      }
    }else if(this.appSetting.deliveryOrOrder===1){
      if(status!==0){
      this.orderModelService.postDelivery(this.invoice,
        this.appSetting.sessionUser.userID,status);
        this.orderTracking=0;
      }
     
    }
  
   
  }
  ngOnInit(): void {
    this.appSetting.checkOut=0;
    
  }

  deleteItem(id:number){
    this.appSetting.localViewShopcart.splice(id,1);
    this.appSetting.localShopcart.splice(id,1);
   
  }
  onDelete(id:number){
      this.deleteItem(id);
      
  }
  cart(){

  }
  checkOut(){
    if(this.appSetting.localShopcart.length!==0){
      this.appSetting.checkOut=1;
     
      this.payment();
      
    }
  }

  payment(){
    if(this.appSetting.sessionUser.userID!==0){
    this.appSetting.localShopcart.forEach(x=>{
      x.userID=this.appSetting.sessionUser.userID;
    });
    this.orderService.post(this.appSetting.localShopcart);
    this.geolocation.getCurrentPosition().then((resp) => {
      let locationModel:locationModel={
        id:0,
        userID:this.appSetting.sessionUser.userID,
        location_ltd:resp.coords.latitude.toString(),
        location_lng:resp.coords.longitude.toString()
      }
      this.locationService.post(locationModel);
     
     }).catch((error) => {
       console.log('Error getting location', error);
     });
  }
  else
  {console.log("error")}}
  totalCalculatorForOrderTracking(){
    this.totalQty=0;
    this.totalAmount=0;
    this.appSetting.userOrderTrackingListTemp.forEach(x => {
      this.totalAmount=this.totalAmount+x.output_price;
      this.totalQty=this.totalQty+x.outputQty;
      
    });
    let res:total={qty:this.totalQty,amount:this.totalAmount}
    return res;
  }
  totalCalculator(){
    this.totalQty=0;
    this.totalAmount=0;
    this.appSetting.localShopcart.forEach(x => {
      this.totalAmount=this.totalAmount+x.output_price;
      this.totalQty=this.totalQty+x.outputQty;
    });
  }

  checkLocationTesting(){
    this.geolocation.getCurrentPosition().then((resp) => {
      console.log(resp.coords.latitude);
      console.log(resp.coords.longitude);
      Swal.fire("Latitude is"+resp.coords.latitude+" and longitude is"+resp.coords.longitude)
     }).catch((error) => {
       console.log('Error getting location', error);
     });
  }

  oneEditQty(id,mainItemID){
    let shopCartItem:orderQtyModels=this.appSetting.localShopcart[id];
    let shopCartItemView:orderQtyViewModel=this.appSetting.localViewShopcart[id];
    console.log(shopCartItem);
    console.log(shopCartItemView);
    let priceTemp:priceModel[]=[];
    Swal.fire({
      title: 'Please wait',
      html: 'System is processing for your request',
      onBeforeOpen: () => {
        Swal.showLoading()}});
    this.UnitService.get(+mainItemID).subscribe(y=>priceTemp=y,
      err=>console.log(err),
      ()=>{
        Swal.close();
            let priceShopcart:number=0;
              Swal.mixin({
              confirmButtonText: 'Next &rarr;',
              showCancelButton: true,
              progressSteps: ['1']
              }).
              queue([{
              title: 'Quantity',
              onRender:()=>{
              let selectDom=document.getElementsByClassName('swal2-input')[0] as HTMLInputElement;
              if(selectDom!==undefined){
              selectDom.oninput=()=>{
                Swal.showLoading();
                let qty:number=+selectDom.value;
              
                let priceArray:number[]=[];
                let qtyArray:number[]=[];
                priceTemp.forEach(p=>{
                    priceArray.push(p.price);
                    qtyArray.push(p.qty);
                }
                  )
              
                priceArray.reverse();
                qtyArray.reverse();
            
                let price:number=0;
                for(var i=0;i<priceArray.length;i++){
                  
                    let qtyTemp:number=+((qty/(+qtyArray[i])).toString());
                  
                    if(qtyTemp<1)
                    {
                      qtyTemp=0;
                    }
                    let toCut:number=qtyTemp.toString().indexOf(".");
                    
                    if(toCut>0){
                      qtyTemp=+(qtyTemp.toString().substring(0,toCut+1));
                    }
                    price=price+(qtyTemp*(+priceArray[i]));
                    if(price!==0){
                    qty=qty%(+qtyArray[i]);
                    }
                    
                }
                document.getElementById('swal2-title').innerText=price.toString()+' Kyats';
              
                priceShopcart=price;
                selectDom.focus();
                Swal.hideLoading();
            };
        
            }
            else
            {
              console.log("Sak it is undefined");
            }
            },
            text: 'Please insert amount to add quantity!',
            input:'number',
            inputValidator: (value) => {
              Swal.showLoading();
              return new Promise((resolve) => {
                if(value!==null && (+value)>0){
                  shopCartItem.output_price=+priceShopcart;
                  shopCartItem.outputQty=+value;
                  shopCartItemView.output_price=+priceShopcart;
                  shopCartItemView.outputQty=+value;
                  this.appSetting.localShopcart[id]=shopCartItem;
                  this.appSetting.localViewShopcart[id]=shopCartItemView;
                  Swal.hideLoading();
                  Swal.fire("Success","Your item qty has been updated",'success');
                  //resolve();
                } 
                else{
                  Swal.hideLoading();
                  Swal.fire("Please Check the Qty","Your need to add atleast 1",'info');
                }
              })
            
            }
              }]);
              this.totalCalculator();
             
          })
  }
}

interface total{
  qty:number;
  amount:number;
}

interface locationInterface{location_ltd:string,location_lng:string};