import { UserOrderTrackingService } from './../userOrderTracking/user-order-tracking.service';
import { orderModel } from './../../model/orderModel';
import { appSetting } from 'src/app/app-setting';
import Swal  from 'sweetalert2';
import { HttpClient } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class OrderService {
  private url = `${this.appSetting.http}://${this.appSetting.apiPort}/api/OrderModels`;
  private deliveryURI = `${this.appSetting.http}://${this.appSetting.apiPort}/api/deliveryModels`;
  private httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json'
    })
  };
  constructor(private http: HttpClient,private appSetting:appSetting,private orderTrackingService:UserOrderTrackingService) {}
  get(): Observable<orderModel[]> {
    
    return this.http.get<orderModel[]>(this.url);
  }

  getDelivery(): Observable<orderModel[]> {
    return this.http.get<orderModel[]>(this.deliveryURI);
  }
  getSingle(id: number): Observable<orderModel> {
    const searchUrl = `${this.url}/${id}`;
    return this.http.get<orderModel>(searchUrl);
  }
  postDelivery(id,userid,status){
    let data="";
    let url=this.deliveryURI+"/"+id+`?userid=${userid}&status=${status}`;
    Swal.fire({
      title: 'System Message',
      html: 'Your order is processing...',
      confirmButtonText: 'Complete',
      onBeforeOpen: () => {
        Swal.showLoading();
      }
    });
    this.http.post(url, data, this.httpOptions).subscribe(
      res => {
       console.log(res);
      },
      err => {
         Swal.hideLoading();
        Swal.fire({
          title: 'System Message',
          html: 'Oops your internet Connection is not stable...',
          confirmButtonText: 'Ok'
        });
      },()=>{
        this.appSetting.localShopcart=[];
        this.appSetting.localViewShopcart=[];
        this.orderListRefresh();
      }
    );
  }
  post(data: orderModel[]): void {

    Swal.fire({
      title: 'System Message',
      html: 'Your order is processing...',
      confirmButtonText: 'Complete',
      onBeforeOpen: () => {
        Swal.showLoading();
      }
    });
    this.http.post(this.url, data, this.httpOptions).subscribe(
      res => {
       console.log(res);
      },
      err => {
         Swal.hideLoading();
        Swal.fire({
          title: 'System Message',
          html: 'Oops your internet Connection is not stable...',
          confirmButtonText: 'Ok'
        });
      },()=>{
        this.appSetting.localShopcart=[];
        this.appSetting.localViewShopcart=[];
         Swal.hideLoading();
      }
    );
  }

  put(data: orderModel): void {
    const searchUrl = `${this.url}/${data.orderID}`;
    this.http.put(searchUrl, data, this.httpOptions).subscribe(
      res => {
        console.log(res);

      },
      err => {
        console.log(err);
      }
    );
  }

  delete(id: number): void {
    const data = null;
    const xhr = new XMLHttpRequest();
    xhr.withCredentials = false;

    xhr.addEventListener('readystatechange', function() {
      if (this.readyState === this.DONE) {
        console.log(this.responseText);
      }
    });

    xhr.open('DELETE', this.url + '/' + id);

    xhr.send(data);
  }

  orderListRefresh(){
  if(this.appSetting.deliveryOrOrder===1){
      this.orderTrackingService.getDelivery().subscribe(
        x=>this.appSetting.userOrderTrackingList=x,
        err=>console.log(err),()=>{
          Swal.hideLoading();
      })
    }
   }
}
